.details {
  display: none;
}

.show-details {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.details-modal-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* width: 70%; */
  /* height: 80%; */
  width: 80%;
  height: 70%;
  max-width: 800px;
  max-height: 600px;
  background-color: #4d4d4d;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  border-radius: 0.25rem;
}

.details-title {
  flex: 0 0 60px;
  padding: 0.125rem 0.5rem;
  display: flex;
  align-items: center;
  background-color: #000;
  color: yellow;
}
.details-title h2 {
  font-size: 1.5rem;
}

.details-items-list {
  flex: 1;
  background-color: #fff;
  padding: 0.5rem;
  overflow: auto;
}

.details-item-container {
  display: flex;
  flex-direction: column;
  margin: 0.25rem 0;
  padding: 1rem 0.5rem;
  border-top: 2px solid #4d4d4d;
  border-bottom: 2px solid #4d4d4d;
}

.details-item-info {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: bold;
  margin-bottom: 0.25rem;
}
.details-item-info p,
.details-item-info span {
  font-size: 1rem;
  font-weight: bold;
}

.toggle-options-btn {
  background-color: transparent;
  border-color: transparent;
  color: #000;
  font-size: 0.9rem;
  font-weight: bold;
  width: 100%;
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 0.25rem;
}
.options-container {
  display: flex;
  flex-direction: column;
  gap: 0.2rem;
  padding: 0.25rem 0.5rem;
  font-weight: bold;
  font-size: 0.9rem;
  text-transform: capitalize;
  border: 1px solid black;
  border-radius: 0.25rem;
}
.option-item {
  display: flex;
  justify-content: space-between;
}

.option-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.125rem 0;
}
.details-summary {
  display: flex;
  flex-direction: column;
  /* justify-content: space-between; */
  justify-content: center;
  font-weight: bold;
  background-color: #000;
}

.details-totle-price,
.details-total-info {
  font-size: 1.2rem;
  color: yellow;
  padding: 0.35rem 0.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.details-totle-price span,
.details-total-info span {
  margin: 0 0.25rem;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
}

.close-details {
  flex: 0 0 45px;
  font-size: 1rem;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1;
  background-color: #fff;
  border-color: transparent;
  color: #000;
  cursor: pointer;
}
.close-details:hover {
  /* color: #fff; */
  /* background-color: #4d4d4d; */
}
.close-details .icon {
  margin-right: 8px;
  font-size: 1.2rem;
}
